* {
  padding: 0%;
  margin: 0;
  box-sizing: border-box;
}
:root {
}
html {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  text-transform: uppercase;
  font-size: 16px;
}
body {
  background-color: rgb(30, 27, 27);
  color: rgb(234, 234, 243);
}
.App {
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
}

.container {
  max-width: 450px;
  width: 900px;
  margin-inline: auto;

  padding: 2rem 1rem;
}
.shadow-gray {
  box-shadow: 0 20px 0 rgba(0, 0, 0, 0.2);
}
.shadow-blue {
  box-shadow: 0 20px 0 blue;
}
.shadow-yellow {
  box-shadow: 0 20px 0 yellow;
}

/* icons */
.icon {
  display: inline-block;
}

.icon svg {
  fill: rgb(58, 213, 161);
  height: 50px;
  width: 60px;
  vertical-align: middle;
}
.icon-sm svg {
  height: 30px;
  width: 30px;
}
.icon-lg svg {
  height: 65px;
  width: 65px;
}
.icon-yellow svg {
  fill: yellow;
}
.icon-blue svg {
  fill: blue;
}
.icon-dark svg {
  fill: rgb(16, 16, 20);
}

/*start*/
.start {
  display: flex;
  flex-direction: column;
  gap: 25px;
}
.start__header {
  display: flex;
  align-items: center;
  justify-content: center;
}
/*utilites*/
.card {
  padding: 25px;
  display: flex;
  flex-direction: column;
  gap: 25px;
  border-radius: 25px;
  background-color: rgb(44, 46, 47);
  text-align: center;
  box-shadow: 0 8px 0 rgb(12, 12, 12);
}
.text-lg {
  font-size: 1.5rem;
}
.text-xl {
  font-size: 1.5rem;
}
.text-2xl {
  font-size: 1.725rem;
}

.text-light {
  color: rgb(9, 7, 7);
}
text-blue {
  color: blue;
}
.text-yellow {
  color: rgb(124, 212, 17);
}
.bg-light {
  background-color: cadetblue;
  color: rgb(10, 10, 8);
}
.bg-yellow {
  background-color: yellow;
  color: rgb(14, 14, 14);
}
.bg-blue {
  background-color: burlywood;
  color: rgb(6, 6, 5);
}

.btn {
  background: rgb(179, 169, 157);
  border: none;
  font-size: 1.125rem;
  font-weight: bold;
  padding: 12px 25px;
  text-transform: uppercase;
  border-radius: 10px;
  cursor: pointer;
  color: rgba(19, 17, 27, 0.754);
  display: inline-block;
  box-shadow: 0 6px 0 rgb(56, 147, 80);
  margin-bottom: 6px;
  transition: all 0.2s;
}
.btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 0 rgb(56, 147, 80);
}
.btn:active {
  transform: translateY(-2px);
  box-shadow: 0 4px 0 rgb(56, 147, 80);
}
.btn-yellow {
  background-color: yellow;
  box-shadow: 0 6px 0 yellow;
}

.btn-blue {
  background-color: yellow;
  box-shadow: 0 6px 0 yellow;
}
.btn-sm {
  box-shadow: 0 4px 0;
}
.btn-sm:hover {
  box-shadow: 0 6px 0;
}
.btn-sm:active {
  box-shadow: 0 3px 0;
}

.start__players {
  background-color: rgb(33, 37, 37);
  padding: 10px;
  border-radius: 15px;
  display: flex;
  align-items: center;
  box-shadow: inset 0 0 8px rgba(0, 0, 0, 0.2);
}
.start__players span {
  width: 450px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 10px;
  border-radius: 10px;
  cursor: pointer;
}
.start__players--active {
  background-color: rgb(153, 116, 69);
  box-shadow: 0 0 8px burlywood;
}
.start__btns {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
/*board*/

.board__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 50px;
  margin-right: 30px;
}
.board__turn {
  border-radius: 8px;
  background-color: rgb(80, 78, 75);
  display: flex;
  text-align: center;
  align-items: center;
  gap: 10px px;
  box-shadow: 0 10px 0 rgba(0, 0, 0, 0.2);
}
.board__restart svg {
  height: 20px;
  display: flex;
  align-items: center;
  transform-origin: center;
  transition: all 0.3s;
}
.board__restart:active svg {
  transform: rotate(180deg);
}
.board__body {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 25px 20px;
}
.board__body .card {
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding-inline: 10px;
}
.board__body .active.shadow-blue {
  background-color: rgb(68, 54, 82);
}
.board__body .active.shadow-yellow {
  background-color: yellow;
}
.board__footer {
  margin-top: 30px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}
.board__footer .card {
  gap: 15px;
}
/*modal*/
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 999;
  background-color: rgb(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
}
.modal__content {
  width: 100%;
  background-color: rgb(44, 32, 44);
}
/*score*/
.score {
  text-align: center;
}
.score__title {
  font: size 2rem;
  font-size: 30px;
  display: flex;
  align-items: center;
  gap: 6px;
  margin: 10px 0;
  justify-content: center;
}
.score__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
}

/*restart*/
.restart {
  text-align: center;
}
.restart__title {
  font: size 10rem;
  font-size: 30px;
  display: flex;
  align-items: center;
  gap: 6px;
  margin-bottom: 30px;
  justify-content: center;
}
.restart__btns {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
}
